import React, { Component } from 'react';
import {withGetScreen} from 'react-getscreen';
import Bounce from 'react-reveal/Bounce';
import Fade from 'react-reveal/Fade';
import Zoom from 'react-reveal/Zoom';
import Tada from 'react-reveal/Tada';
import MenuCentral from './menuCentral.js'
import LinksFooter from './linksFooter.js'
import './styles/quienesSomos.css';



class SectionLeft extends Component{
  constructor(props){
    super(props);
    this.state = {

    }
  }

  render(){
    return(
      <div id="morado" className="div-seccionQuienesSomos">
        <Fade right>
          <img className="img-seccionQuienesSomos" src={this.props.imagen}/>
        </Fade>

        <Fade left>
          <div className="div-textoSeccionQuienesSomos">
            <h1>{this.props.titulo}</h1>
            <p>{this.props.texto1}</p>
            <p>{this.props.texto2}</p>
            <p>{this.props.texto3}</p>
            <p>{this.props.texto4}</p>
            <p>{this.props.texto5}</p>
            <p>{this.props.texto6}</p>
          </div>
        </Fade>
      </div>
    );
  }
}

class SectionRight extends Component{
  constructor(props){
    super(props);
    this.state = {

    }
  }


  render(){
    return(
      <div id="verde" className="div-seccionQuienesSomos">
        <Fade right>
          <div className="div-textoSeccionQuienesSomos">
            <h1>{this.props.titulo}</h1>

              <p>{this.props.texto1}</p>
              <p>{this.props.texto2}</p>
              <p>{this.props.texto3}</p>
              <p>{this.props.texto4}</p>
              <p>{this.props.texto5}</p>
              <p>{this.props.texto6}</p>

          </div>
        </Fade>

        <Fade left>
          <img className="img-seccionQuienesSomos" src={this.props.imagen}/>
        </Fade>
      </div>
    );
  }
}


class QuienesSomos extends Component {



  constructor(props){
    super(props);
    this.state = {

    }
  }


  render() {


    return (
      <div className="div-principalQuienesSomos">



        <div className="MenuCentral">
          <MenuCentral/>
        </div>

        <SectionLeft titulo="Quines Somos?"
                      texto1="Fundado el 23 de agosto del 2016, por decreto de creacin nmero 25535/LX/15, aprobado por el H. Congreso del Estado de Jalisco, crea el Instituto Tecnolgico Jos Mario Molina Pasquel y Henrquez como organismo pblico descentralizado del Gobierno del Estado con personalidad jurdica y patrimonio propio, el cual tiene la finalidad de prestar servicio de educacin superior tecnolgica en el Estado de Jalisco, adscrito al Tecnolgico Nacional de Mxico y sectorizado a la Secretara de Innovacin Ciencia y Tecnologa"
                      texto2="El Instituto Tecnolgico Jos Mario Molina Pasquel y Henrquez reconocido por sus siglas TecMM. Nombre asignado en honor al Ingeniero Qumico galardonado en el ao 1995 con el Premio Nobel de Qumica por su investigacin de los estudios relacionados con el impacto de la Capa de Ozono."
                      imagen="http://tecmm.edu.mx/imagesReact/images/quienesSomos/quienesSomos1.jpg"/>

        <SectionRight titulo="INICIATIVA"
                      texto1="La iniciativa de crear el TecMM, fue unificar a los 13 Institutos Tecnolgicos Superiores, en un solo Organismo Pblico Descentralizado, con una Direccin General y 13 Unidades Acadmicas en el Estado de Jalisco. Con el propsito de eficientar la estructura acadmica y administrativa, respondiendo a los objetivos del Plan Nacional de Desarrollo y del Plan Estatal de Desarrollo de Jalisco, que establece el compromiso de un Mxico con educacin de calidad."
                      texto2="Nuestra Institucin es heredera de una brillante y firme historia de vida institucional con 20 aos de creacin en el Estado de Jalisco, adscrito al Tecnolgico Nacional de Mxico, siendo el ms grande de Iberoamrica con 600,000 mil estudiantes, 254 institutos en todo el pas, teniendo presencia en ms de 600 localidades en los 32 estados del pas."
                      imagen="http://tecmm.edu.mx/imagesReact/images/quienesSomos/quienesSomos2.jpg"/>

        <SectionLeft titulo="RED TECMM"
                      texto1="En Jalisco somos una red de trece unidades acadmicas y doce extensiones, contamos con una cobertura de Educacin Superior en once de las doce regiones del Estado de Jalisco con ms de 15,000 estudiantes, que nos convierte en la segunda Institucin en el Estado lo que representa el 9.74% de la matrcula de Educacin Superior, con 16 ingenieras, 5 licenciaturas, y 2 posgrados."
                      texto2="Las 13 Unidades Acadmicas del Estado de Jalisco del Instituto Tecnolgico Jos Mario Molina Pasquel y Henrquez son: Arandas, Chapala, Cocula, El Grullo, La Huerta, Lagos de Moreno, Mascota, Puerto Vallarta, Tala, Tamazula, Tequila, Zapopan y Zapotlanejo."
                      imagen="http://tecmm.edu.mx/imagesReact/images/quienesSomos/quienesSomos4.jpg"/>

        <SectionRight titulo="MISIN"
                      texto1="Brindar educacion integral de clase mundial para formar profesionistas competitivos, innovadores y emprendedores y satisfacer las demandas de desarrollo cientifico y tecnologico del sector productivo en cada una de las regiones del estado de Jalisco con proceso de investigacion aplicada y transferencia de tecnologia."
                      texto2=""
                      imagen="http://tecmm.edu.mx/imagesReact/images/quienesSomos/quienesSomos6.jpg"/>

        <SectionLeft titulo="VISIN"
                      texto1="Ser reconocido por su liderazgo en la economia del conocimiento nacional, formando los profesionistas mas talentosos, innovadores y emprendedores del pais y tener los mas altos indices de investigacion e innovacion tecnologica, asi como de la produccion y transferencia de propiedad intelectual entre las instituciones de educacion superior de Mexico."
                      texto2=""
                      imagen="http://tecmm.edu.mx/imagesReact/images/quienesSomos/quienesSomos7.jpg"/>

        <SectionRight titulo="VALORES"
                      texto1="Bienestar Social: Mejorar el acceso, la cobertura y la calidad de la educacion superior, reducir el rezago educativo y promover la equidad en las oportunidades educativas."
                      texto2="Sustentabilidad: Compromiso con la economia y la ecologia de sostenimiento a lo largo del tiempo sin agotar los recursos o perjudicar el medio ambiente."
                      texto3="Integridad: La capacidad de actuar en consecuencia con lo que se dice o lo que se considera que es importante, tiene todas sus partes enteras y no afecta los intereses de las personas ni la institucion."
                      texto4="Innovacion: De manera continua buscamos introducir en todos los ambitos del quehacer de nuestro Instituto, cambios que representen una mejora, un progreso y sobre todo un mayor valor para los receptores de nuestros servicios y productos (clientes internos y externos) y de todo nuestro ecosistema."
                      texto5="Competitividad: Es la cualidad con la cual se busca la realizacion de los objetivos y generar los mejores resultados educativos, generando conocimiento para el desarrollo de las personas y su entorno."
                      texto6="Emprendimiento: Generar actividades y estrategias para establecer el ambiente emprendedor, estimulando la iniciativa de los estudiantes para desarrollar un proyecto de negocio, generando el sentido de emprendimiento y generacion de ideas creativas viables, para que se conviertan en una realidad."
                      imagen="http://tecmm.edu.mx/imagesReact/images/quienesSomos/quienesSomos5.jpg"/>






        <LinksFooter/>

      </div>
    );
  }
}

const options = {mobileLimit: 420, tabletLimit: 800}
export default withGetScreen(QuienesSomos, options);
